import React from 'react';
import {
  CLAUSE_OF_PRIVACY_URL,
  CLAUSE_OF_SERVICE_URL,
} from '../../config/pageUrl';
import { AGREE_CLAUSE_TO_CREATE } from '../../config/copywriting';
import copywritingHelper from '../../utils/copywritingHelper';

import styles from './index.module.scss';

const AgreeClauseToCreate = ({copywriting = AGREE_CLAUSE_TO_CREATE}) => (
  <p className={styles.agreeClauseToCreate}>
    {copywritingHelper(copywriting).map((item, index) => {
      if (index === 1) {
        return (
          <a
            className={styles.clauseLink}
            href={CLAUSE_OF_SERVICE_URL}
            target="_blank"
            rel="noopener noreferrer"
            key={index}
          >
            {item}
          </a>
        );
      }
      if (index === 3) {
        return (
          <a
            className={styles.clauseLink}
            href={CLAUSE_OF_PRIVACY_URL}
            target="_blank"
            rel="noopener noreferrer"
            key={index}
          >
            {item}
          </a>
        );
      }
      return item;
    })}
  </p>
);

export default AgreeClauseToCreate;
